.nav-bar {
  background-color: rgb(36, 36, 36);
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 2rem;
}
.nav-bar-list {
  display: flex;
  list-style: none;
}
.nav-bar a {
  padding: 1rem;
  color: white;
  font-size: 1.5rem;
  text-decoration: none;
  display: block;
}

.nav-bar a:hover {
  background-color: hsl(120, 34%, 32%);
}

@media only screen and (max-width: 760px) {
  .nav-bar {
    padding: 0;
  }
  .nav-bar a {
    padding: 1rem 0.5rem;
    font-size: 1.2rem;
  }
}
